import moment from 'moment';
import React, { FC, useEffect } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { currentDate, formatDate, InputGroupInline, InputType, WindowComponent, WindowComponentProps } from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../../dependencies/empresaMFPDependency';
import { hasGrupoEmpresaConfig } from '../../../../../dependencies/grupoEmpresaConfigDependency';
import { getDefaultReportBasicValues } from '../../../../../models/dtos/ger/reportBasicValues';
import { getDefaultReportViewOptions, ReportViewOptions } from '../../../../../models/dtos/ger/reportViewOptions';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';
import CustomizacaoTermoIS from '../../../../components/mfp/customizacaoTermoIS/CustomizacaoTermoIS';
import VinculoIS from '../../../../components/mfp/vinculoIS/VinculoIS';
import ReportForm from '../../../../components/report/ReportForm';
import { CLASS_KEY, REPORT_EVENT, REPORT_TITLE, TermoAditivoRV } from './termoAditivoRV';

const DEFAULT_VALUES: TermoAditivoRV = {
  ...getDefaultReportBasicValues(REPORT_TITLE),
  vinculosSelecionados: [],
};

const DEFAULT_OPTIONS: ReportViewOptions = {
  ...getDefaultReportViewOptions(REPORT_EVENT),
  exibirConfiguracaoDadosEmpresa: false,
};

const TermoAditivoReportView: FC<WindowComponentProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { handleSubmit, ...formProps } = useForm<TermoAditivoRV>({ defaultValues: DEFAULT_VALUES, mode: 'onBlur' });

  useEffect(
    () => {
      setTimeout(() => {
        formProps.setValue('data', formatDate(currentDate()), true);
      }, 1000);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  function validateExtras(): string[] {
    const termoAditivoRV: TermoAditivoRV = formProps.getValues();
    const errors = [];

    termoAditivoRV.vinculosSelecionados.forEach(vinculo => {
      if (moment(termoAditivoRV.data, 'DD/MM/YYYY').isBefore(moment(vinculo.dataAdmissao, 'DD/MM/YYYY'))) {
        errors.push('A data para o Termo Aditivo é anterior à Data de Admissão de todos os Trabalhadores Cargo selecionados.');
      }
    });

    return errors;
  }

  return (
    <WindowComponent
      title="Termo Aditivo"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[
        () => hasEmpresa(globalReducer.globalParameter),
        () => hasEmpresaMFP(globalReducer.globalParameter),
        () => hasGrupoEmpresaConfig(globalReducer.globalParameter),
      ]}
      wikiId={5162}
      wikiTitle={'Relatório - Termo Aditivo de Contrato'}
    >
      <ReportForm
        {...props}
        handleSubmit={handleSubmit}
        formProps={formProps}
        localStorageKey={CLASS_KEY}
        routeProps={props}
        reportBasicValues={DEFAULT_VALUES}
        reportViewOptions={DEFAULT_OPTIONS}
        validateExtras={validateExtras}
      >
        <CustomizacaoTermoIS
          formProps={{ ...formProps, validation: { required: true } }}
          label="Termo"
          searchViewProps={{ apenasTermoAditivo: true }}
          apenasTermoAditivo={true}
          placeholder="Selecione o Termo"
          id="termo"
          isPrependLabel={true}
          inputSearchProps={{ size: 'xl' }}
        />

        <VinculoIS
          id="vinculosSelecionados"
          formProps={{ ...formProps, validation: { required: true } }}
          label="Trabalhador"
          isPrependLabel={true}
          inputSearchProps={{ selectProps: { isMulti: true } }}
          placeholder="Selecione o Trabalhador(es)"
        />
        <InputGroupInline id="data" placeholder="Informe a Data" formProps={formProps} isPrependLabel={true} label="Data" type={InputType.DATE} />
      </ReportForm>
    </WindowComponent>
  );
};

export default TermoAditivoReportView;
